export default "import {FirstArgument, IsTypeEqual, typeAssert} from 'type-assertions';\nimport {logPerson, Person, persons} from './index';\n\n\ntypeAssert<\n    IsTypeEqual<\n        Person,\n        {name: string; age: number} & ({occupation: string} | {role: string})\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof persons,\n        ({name: string; age: number} & ({occupation: string} | {role: string}))[]\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        FirstArgument<typeof logPerson>,\n        {name: string; age: number} & ({occupation: string} | {role: string})\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        ReturnType<typeof logPerson>,\n        void\n    >\n>();\n";