export default "import {IsTypeEqual, typeAssert} from 'type-assertions';\nimport {swap} from './index';\n\nconst pair1 = swap(123, 'hello');\ntypeAssert<\n    IsTypeEqual<\n        typeof pair1,\n        [string, number]\n    >\n>();\n\nconst pair2 = swap(true as const, false as const);\ntypeAssert<\n    IsTypeEqual<\n        typeof pair2,\n        [false, true]\n    >\n>();\n\nconst pair3 = swap(null, undefined);\ntypeAssert<\n    IsTypeEqual<\n        typeof pair3,\n        [undefined, null]\n    >\n>();\n";