export default "import {IsTypeEqual, IsTypeAssignable, FirstArgument, typeAssert} from 'type-assertions';\nimport {api, promisify, ApiResponse} from './index';\n\ntypeAssert<\n    IsTypeAssignable<\n        FirstArgument<typeof promisify>,\n        (callback: (response: ApiResponse<number>) => void) => void\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        FirstArgument<typeof promisify>,\n        (callback: (response: ApiResponse<string>) => void) => void\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        ReturnType<typeof promisify>,\n        () => Promise<number>\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        ReturnType<typeof promisify>,\n        () => Promise<boolean>\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof api.requestAdmins,\n        () => Promise<\n            {\n                type: 'admin';\n                name: string;\n                age: number;\n                role: string;\n            }[]\n        >\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof api.requestUsers,\n        () => Promise<\n            {\n                type: 'user';\n                name: string;\n                age: number;\n                occupation: string;\n            }[]\n        >\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof api.requestCurrentServerTime,\n        () => Promise<number>\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof api.requestCoffeeMachineQueueLength,\n        () => Promise<number>\n    >\n>();\n\n";