export default "import {IsTypeEqual, typeAssert} from 'type-assertions';\nimport {\n    getMaxIndex,\n    getMaxElement,\n    getMinIndex,\n    getMinElement,\n    getMedianIndex,\n    getMedianElement,\n    getAverageValue\n} from './index';\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getMaxIndex,\n        <T>(input: T[], comparator: (a: T, b: T) => number) => number\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getMinIndex,\n        <T>(input: T[], comparator: (a: T, b: T) => number) => number\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getMedianIndex,\n        <T>(input: T[], comparator: (a: T, b: T) => number) => number\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getMaxElement,\n        <T>(input: T[], comparator: (a: T, b: T) => number) => T | null\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getMinElement,\n        <T>(input: T[], comparator: (a: T, b: T) => number) => T | null\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getMedianElement,\n        <T>(input: T[], comparator: (a: T, b: T) => number) => T | null\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof getAverageValue,\n        <T>(input: T[], getValue: (item: T) => number) => number | null\n    >\n>();\n";