export default "import {IsTypeEqual, IsTypeAssignable, Not, typeAssert} from 'type-assertions';\nimport {\n    ApiResponse,\n    requestAdmins,\n    requestUsers,\n    requestCoffeeMachineQueueLength,\n    requestCurrentServerTime\n} from './index';\n\ntypeAssert<\n    IsTypeAssignable<\n        ApiResponse<number>,\n        {status: 'success'; data: number}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        ApiResponse<number>,\n        {status: 'error'; error: string}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        ApiResponse<boolean>,\n        {status: 'success'; data: boolean}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        ApiResponse<boolean>,\n        {status: 'error'; error: string}\n    >\n>();\ntypeAssert<\n    Not<\n        IsTypeEqual<\n            ApiResponse<number>,\n            unknown\n        >\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof requestAdmins,\n        (\n            callback: (\n                response: {\n                    status: 'success';\n                    data: {\n                            type: 'admin';\n                            name: string;\n                            age: number;\n                            role: string;\n                    }[]\n                } | {\n                    status: 'error';\n                    error: string;\n                }\n            ) => void\n        ) => void\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof requestUsers,\n        (\n            callback: (\n                response: {\n                    status: 'success';\n                    data: {\n                            type: 'user';\n                            name: string;\n                            age: number;\n                            occupation: string;\n                    }[]\n                } | {\n                    status: 'error';\n                    error: string;\n                }\n            ) => void\n        ) => void\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof requestCurrentServerTime,\n        (\n            callback: (\n                response: {\n                    status: 'success';\n                    data: number;\n                } | {\n                    status: 'error';\n                    error: string;\n                }\n            ) => void\n        ) => void\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof requestCoffeeMachineQueueLength,\n        (\n            callback: (\n                response: {\n                    status: 'success';\n                    data: number;\n                } | {\n                    status: 'error';\n                    error: string;\n                }\n            ) => void\n        ) => void\n    >\n>();\n";