export default "import {IsTypeEqual, FirstArgument, typeAssert} from 'type-assertions';\nimport {logPerson, Person, persons} from './index';\n\ntypeAssert<\n    IsTypeEqual<\n        Person,\n        {name: string; age: number} & (\n            | {type: 'user'; occupation: string}\n            | {type: 'admin'; role: string}\n            | {type: 'powerUser'; occupation: string; role: string}\n        )\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof persons,\n        ({name: string; age: number} & (\n            | {type: 'user'; occupation: string}\n            | {type: 'admin'; role: string}\n            | {type: 'powerUser'; occupation: string; role: string}\n        ))[]\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        FirstArgument<typeof logPerson>,\n        {name: string; age: number} & (\n            | {type: 'user'; occupation: string}\n            | {type: 'admin'; role: string}\n            | {type: 'powerUser'; occupation: string; role: string}\n        )\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        ReturnType<typeof logPerson>,\n        void\n    >\n>();\n";