export default "import {IsTypeEqual, IsTypeAssignable, Not, FirstArgument, SecondArgument, typeAssert} from 'type-assertions';\nimport {logPerson, isUser, isAdmin, Person, persons, filterUsers} from './index';\n\ntypeAssert<\n    IsTypeAssignable<\n        SecondArgument<typeof filterUsers>,\n        {name: string}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        SecondArgument<typeof filterUsers>,\n        {age: number}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        SecondArgument<typeof filterUsers>,\n        {name: string; age: number}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        SecondArgument<typeof filterUsers>,\n        {occupation: string}\n    >\n>();\ntypeAssert<\n    IsTypeAssignable<\n        SecondArgument<typeof filterUsers>,\n        {name: string; age: number; occupation: string}\n    >\n>();\ntypeAssert<\n    Not<\n        IsTypeAssignable<\n            SecondArgument<typeof filterUsers>,\n            {hello: 'world'}\n        >\n    >\n>();\ntypeAssert<\n    IsTypeEqual<\n        ReturnType<typeof filterUsers>,\n        {type: 'user'; name: string; age: number; occupation: string}[]\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        Person,\n        {name: string; age: number} & ({type: 'user'; occupation: string} | {type: 'admin'; role: string})\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof persons,\n        ({name: string; age: number} & ({type: 'user'; occupation: string} | {type: 'admin'; role: string}))[]\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        FirstArgument<typeof logPerson>,\n        {name: string; age: number} & ({type: 'user'; occupation: string} | {type: 'admin'; role: string})\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        ReturnType<typeof logPerson>,\n        void\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        FirstArgument<typeof isUser>,\n        {name: string; age: number} & ({type: 'user'; occupation: string} | {type: 'admin'; role: string})\n    >\n>();\ntypeAssert<\n    IsTypeEqual<\n        ReturnType<typeof isUser>,\n        boolean\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        FirstArgument<typeof isAdmin>,\n        {name: string; age: number} & ({type: 'user'; occupation: string} | {type: 'admin'; role: string})\n    >\n>();\ntypeAssert<\n    IsTypeEqual<\n        ReturnType<typeof isAdmin>,\n        boolean\n    >\n>();\n";