export default "import {IsTypeEqual, typeAssert} from 'type-assertions';\nimport {dateWizard} from './index';\n\ntypeAssert<\n    IsTypeEqual<\n        dateWizard.DateDetails,\n        {\n            year: number;\n            month: number;\n            date: number;\n            hours: number;\n            minutes: number;\n            seconds: number;\n        }\n    >\n>();\n\ntypeAssert<\n    IsTypeEqual<\n        typeof dateWizard.pad,\n        (level: number) => string\n    >\n>();\n";